.header {
  padding-top: 12px;
  padding-bottom: 12px;
}
@media (min-width: 768px) {
  .header {
    padding-top: 20px;
    padding-bottom: 20px;
  }
}
@media (min-width: 1440px) {
  .header {
    padding-top: 40px;
    padding-bottom: 40px;
  }
}
.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__logo {
  margin-right: 24px;
}
.header__logo:hover svg {
  fill: var(--orange-cl);
}
.header__logo svg {
  flex-shrink: 0;
  width: 36px;
  height: 33px;
  fill: var(--white-cl);
  transition: fill 0.2s ease;
}
.header__nav {
  display: none;
}
@media (min-width: 992px) {
  .header__nav {
    display: block;
  }
}
@media (min-width: 992px) {
  .header__menu {
    display: none;
  }
}
.header__menu svg {
  fill: var(--white-cl);
  height: 40px;
  width: 40px;
}

.link {
  text-decoration: none;
  color: var(--white-cl);
  font-size: 12px;
  font-weight: 500;
  font-family: var(--font-family-acent);
  transition: color 0.2s ease;
}
.link:hover {
  color: var(--orange-cl);
}
.link--active {
  color: var(--orange-cl);
}

.main-nav__list {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
  display: grid;
  grid-gap: 24px;
}
@media (min-width: 992px) {
  .main-nav__list {
    grid-gap: 30px;
    grid-auto-flow: column;
  }
}
.main-nav__item {
  display: flex;
  align-items: center;
}
.page {
  display: flex;
  flex-direction: column;
  height: 100%;
  min-height: 100%;
}
.page__header {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 97;
  transition: background-color 0.2s ease;
}
.page__main {
  flex-grow: 1;
  flex-shrink: 0;
}
.page__nav {
  height: 100%;
  left: 0;
  position: fixed;
  top: 0;
  width: 0;
  z-index: 99;
}
.page__darker {
  height: 100%;
  left: 0;
  opacity: 0;
  pointer-events: none;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 98;
}
@media (min-width: 992px) {
  .page__darker {
    display: none;
  }
}
.open-nav .page__darker {
  pointer-events: auto;
}

.section {
  background-repeat: no-repeat, no-repeat;
  background-size: contain, cover;
  position: relative;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  padding-bottom: 50px;
  padding-top: 50px;
}
@media (min-width: 768px) {
  .section {
    background-size: 620px, cover;
    background-position: left -100px center, center;
  }
}
@media (min-width: 992px) {
  .section {
    background-size: 780px, cover;
    background-position: left -120px center, center;
  }
}
@media (min-width: 1440px) {
  .section {
    background-size: 980px, cover;
    background-position: left -150px center, center;
  }
}
@media (min-width: 1920px) {
  .section {
    background-size: 1200px, cover;
    background-position: left 10% center, center;
  }
}
.section__inner {
  padding-top: 80%;
  padding-bottom: 66px;
  flex-grow: 1;
}
@media (min-width: 768px) {
  .section__inner {
    padding-top: 206px;
    align-items: center;
    display: flex;
  }
}
.section__content {
  position: relative;
  z-index: 2;
}
.section__tag {
  margin-top: 0;
  margin-bottom: 0;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.2;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  color: inherit;
}
@media (min-width: 1440px) {
  .section__tag {
    font-size: 20px;
  }
}
.section__tag--link {
  transition: color 0.2s ease;
}
.section__tag--link:hover {
  text-decoration: none;
}
.section__heading {
  font-size: 34px;
  margin-top: 0;
  margin-bottom: 0;
  line-height: 1.09;
}
@media (min-width: 992px) {
  .section__heading {
    font-size: 48px;
  }
}
@media (min-width: 1440px) {
  .section__heading {
    font-size: 58px;
  }
}
@media (min-width: 1920px) {
  .section__heading {
    font-size: 68px;
  }
}
@media (min-width: 768px) {
  .section__list-set {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 24px;
  }
}
.section__list {
  list-style: none;
  margin-bottom: 0;
  padding-left: 0;
}
.section__tag + .section__heading, .section__list + .section__tag, .section__list-set + .section__tag {
  margin-top: 20px;
}
.section__list-item + .section__list-item {
  margin-top: 12px;
}
.section__list, .section__text {
  margin-top: 24px;
  margin-bottom: 0;
  font-size: 16px;
  line-height: 1.6;
}
@media (min-width: 1440px) {
  .section__list, .section__text {
    line-height: 1.8;
  }
}
@media (min-width: 1920px) {
  .section__list, .section__text {
    font-size: 20px;
  }
}
.section__list a,
.section__list span, .section__text a,
.section__text span {
  font-weight: 700;
}
.section__list a:hover, .section__text a:hover {
  text-decoration: none;
}
.section__map {
  margin-top: 24px;
  margin-bottom: 20px;
}
.section__map iframe {
  width: 100%;
  border: none;
}
.section--skull {
  background-image: url("../img/spooky-skull.png"), var(--skull-bg-cl);
}
.section--skull .section__list span,
.section--skull .section__text span {
  color: var(--red-pink-cl);
}
.section--skull .section__tag {
  color: var(--red-pink-cl);
}
.section--portrait {
  background-image: url("../img/portrait.png"), var(--skull-bg-cl);
}
.section--portrait .section__list span,
.section--portrait .section__text span {
  color: var(--red-pink-cl);
}
.section--portrait .section__tag {
  color: var(--red-pink-cl);
}
.section--pumpkin {
  background-image: url("../img/pumpkin.png"), var(--pumpkin-bg-cl);
}
.section--pumpkin .section__list span,
.section--pumpkin .section__text span {
  color: var(--green-cl);
}
.section--pumpkin .section__tag {
  color: var(--white-cl);
}
.section--ghost {
  background-image: url("../img/ghosts.png"), var(--ghost-bg-cl);
}
@media (min-width: 768px) {
  .section--ghost {
    background-size: 520px, cover;
    background-position: right -180px center, center;
  }
}
@media (min-width: 992px) {
  .section--ghost {
    background-size: 680px, cover;
    background-position: right -190px center, center;
  }
}
@media (min-width: 1440px) {
  .section--ghost {
    background-size: 880px, cover;
    background-position: right -290px center, center;
  }
}
@media (min-width: 1920px) {
  .section--ghost {
    background-size: 1100px, cover;
  }
}
.section--ghost .section__heading,
.section--ghost .section__text,
.section--ghost .section__tag {
  color: var(--calm-dark-cl);
}
.section--ghost .section__text {
  margin-bottom: 20px;
}
.section--ghost .section__text a,
.section--ghost .section__text span {
  color: var(--white-cl);
}
@media (min-width: 992px) {
  .section--ghost .section__img {
    margin-left: auto;
    right: 0;
    margin-right: -100px;
  }
}
.section--ghost .section__tag--link:hover {
  color: var(--white-cl);
}
@media (min-width: 768px) {
  .section--horisontal {
    background-position: left -80px top -80px, center;
  }
}
@media (min-width: 1440px) {
  .section--horisontal {
    background-position: left 10% top -120px, center;
  }
}
@media (min-width: 768px) {
  .section--horisontal .section__inner {
    display: block;
    padding-top: 450px;
  }
}
@media (min-width: 992px) {
  .section--horisontal .section__inner {
    padding-top: 600px;
  }
}
@media (min-width: 1440px) {
  .section--horisontal .section__inner {
    padding-top: 750px;
  }
}
@media (min-width: 1920px) {
  .section--horisontal .section__inner {
    padding-top: 900px;
  }
}
@media (min-width: 768px) {
  .section--horisontal .section__img {
    position: static;
  }
}

.side-nav {
