// В этом файле должны быть стили для БЭМ-блока header, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.header {
	padding-top: 12px;
	padding-bottom: 12px;

	@media (min-width: $screen-sm) {
		padding-top: 20px;
		padding-bottom: 20px;
	}

	@media (min-width: $screen-lg) {
		padding-top: 40px;
		padding-bottom: 40px;
	}

	&__inner {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	&__logo {
		margin-right: 24px;

		&:hover {
			svg {
				fill: var(--orange-cl);
			}
		}

		svg {
			flex-shrink: 0;
			width: 36px;
			height: 33px;
			fill: var(--white-cl);
			transition: fill .2s ease;
		}
	}

	&__nav {
		display: none;

		@media (min-width: $screen-md) {
			display: block;
		}
	}

	&__menu {
		@media (min-width: $screen-md) {
			display: none;
		}

		svg {
			fill: var(--white-cl);
			height: 40px;
			width: 40px;
		}
	}
}
