// В этом файле должны быть стили для БЭМ-блока section, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.section {
	background-repeat: no-repeat, no-repeat;
	background-size: contain, cover;
	position: relative;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	padding-bottom: 50px;
	padding-top: 50px;

	@media (min-width: $screen-sm) {
		background-size: 620px, cover;
		background-position: left -100px center, center;
	}

	@media (min-width: $screen-md) {
		background-size: 780px, cover;
		background-position: left -120px center, center;
	}

	@media (min-width: $screen-lg) {
		background-size: 980px, cover;
		background-position: left -150px center, center;
	}

	@media (min-width: $screen-xl) {
		background-size: 1200px, cover;
		background-position: left 10% center, center;
	}

	&__inner {
		padding-top: 80%;
		padding-bottom: 66px;
		flex-grow: 1;

		@media (min-width: $screen-sm) {
			padding-top: 206px;
			align-items: center;
			display: flex;
		}
	}

	&__content {
		position: relative;
		z-index: 2;
	}

	&__tag {
		margin-top: 0;
		margin-bottom: 0;
		font-weight: 700;
		font-size: 16px;
		line-height: 1.2;
		letter-spacing: 0.03em;
		text-transform: uppercase;
		color: inherit;

		@media (min-width: $screen-lg) {
			font-size: 20px;
		}

		&--link {
			transition: color .2s ease;
			
			&:hover {
				text-decoration: none;
			}
		}
	}

	&__heading {
		font-size: 34px;
		margin-top: 0;
		margin-bottom: 0;
		line-height: 1.09;

		@media (min-width: $screen-md) {
			font-size: 48px;
		}

		@media (min-width: $screen-lg) {
			font-size: 58px;
		}

		@media (min-width: $screen-xl) {
			font-size: 68px;
		}
	}

	&__list-set {
		@media (min-width: $screen-sm) {
			display: grid;
			grid-template-columns: 1fr 1fr;
			grid-gap: 24px;
		}
	}

	&__list {
		list-style: none;
		margin-bottom: 0;
		padding-left: 0;
	}

	&__tag + &__heading,
	&__list + &__tag,
	&__list-set + &__tag {
		margin-top: 20px;
	}

	&__list-item {
		& + & {
			margin-top: 12px;
		}
	}

	&__list,
	&__text {
		margin-top: 24px;
		margin-bottom: 0;
		font-size: 16px;
		line-height: 1.6;

		@media (min-width: $screen-lg) {
			line-height: 1.8;
		}

		@media (min-width: $screen-xl) {
			font-size: 20px;
		}

		a,
		span {
			font-weight: 700;
		}

		a:hover {
			text-decoration: none;
		}
	}

	&__map {
		margin-top: 24px;
		margin-bottom: 20px;

		iframe {
			width: 100%;
			border: none;
		}
	}

	&--skull {
		background-image: url('../img/spooky-skull.png'), var(--skull-bg-cl);

		.section__list,
		.section__text {
			span {
				color: var(--red-pink-cl);
			}
		}

		.section__tag {
			color: var(--red-pink-cl);
		}
	}

	&--portrait {
		background-image: url('../img/portrait.png'), var(--skull-bg-cl);

		.section__list,
		.section__text {
			span {
				color: var(--red-pink-cl);
			}
		}

		.section__tag {
			color: var(--red-pink-cl);
		}
	}

	&--pumpkin {
		background-image: url('../img/pumpkin.png'), var(--pumpkin-bg-cl);

		.section__list,
		.section__text {
			span {
				color: var(--green-cl);
			}
		}

		.section__tag {
			color: var(--white-cl);
		}
	}

	&--ghost {
		background-image: url('../img/ghosts.png'), var(--ghost-bg-cl);

		@media (min-width: $screen-sm) {
			background-size: 520px, cover;
			background-position: right -180px center, center;
		}

		@media (min-width: $screen-md) {
			background-size: 680px, cover;
			background-position: right -190px center, center;
		}

		@media (min-width: $screen-lg) {
			background-size: 880px, cover;
			background-position: right -290px center, center;
		}

		@media (min-width: $screen-xl) {
			background-size: 1100px, cover;
		}

		.section__heading,
		.section__text,
		.section__tag {
			color: var(--calm-dark-cl);
		}

		.section__text {
			margin-bottom: 20px;

			a,
			span {
				color: var(--white-cl);
			}
		}

		.section__img {
			@media (min-width: $screen-md) {
				margin-left: auto;
				right: 0;
				margin-right: -100px;
			}
		}

		.section__tag--link {
			&:hover {
				color: var(--white-cl);
			}
		}
	}

	&--horisontal {
		@media (min-width: $screen-sm) {
			background-position: left -80px top -80px, center;
		}

		@media (min-width: $screen-lg) {
			background-position: left 10% top -120px, center;
		}

		.section__inner {
			@media (min-width: $screen-sm) {
				display: block;
				padding-top: 450px;
			}

			@media (min-width: $screen-md) {
				padding-top: 600px;
			}

			@media (min-width: $screen-lg) {
				padding-top: 750px;
			}

			@media (min-width: $screen-xl) {
				padding-top: 900px;
			}
		}

		.section__img {
			@media (min-width: $screen-sm) {
				position: static;
			}
		}
	}
}
