:root {


--font-family-base:				var(--font-family-sans);
--font-family-sans:				'Poppins', sans-serif, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial;
--font-family-acent:			'Roboto', sans-serif, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial;

--typography-cl:				var(--white-cl);

--skull-bg-cl:					linear-gradient(136.16deg, rgba(46, 9, 22, .97) 12.91%, rgba(32, 10, 43, .69) 87.68%);
--pumpkin-bg-cl:				linear-gradient(136.16deg, rgba(91, 135, 75, .97) 12.91%, rgba(12, 55, 32, .87) 87.68%);
--ghost-bg-cl:					linear-gradient(130.02deg, #e8cafb .66%, #8a87c1 83.32%);

--font-size-base:				1rem;
--font-size-root:				null;

--font-weight-base:				400;
--line-height-base:				1;

--white-cl:						#ffffff;
--black-cl:						#000000;
--ca-black-60: 					rgba(35, 35, 35, 0.6);
--red-pink-cl:					#ff5b79;
--magenta-dark-cl:				#2e0916ff;
--green-cl:						#57f324;
--orange-cl:					#fc7334;
--calm-dark-cl:					#303056;


}