// В этом файле должны быть стили для БЭМ-блока page, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.page {
	display: flex;
	flex-direction: column;
	height: 100%;
	min-height: 100%;

	&__header {
		position: fixed;
		top: 0;
		width: 100%;
		z-index: 97;
		transition: background-color .2s ease;
	}

	&__main {
		flex-grow: 1;
		flex-shrink: 0;
	}

	&__nav {
		height: 100%;
		left: 0;
		position: fixed;
		top: 0;
		width: 0;
		z-index: 99;
	}

	&__darker {
		height: 100%;
		left: 0;
		opacity: 0;
		pointer-events: none;
		position: fixed;
		top: 0;
		width: 100%;
		z-index: 98;

		@media (min-width: $screen-md) {
			display: none;
		}

		.open-nav & {
			pointer-events: auto;
		}
	}
}
